









import Vue from 'vue';
import GreatProject from '../../Components/greatProject.vue';
import ExpandingScrollList from '../../Components/expandingScrollList.vue';
import { getFile } from '../../appData';

export default Vue.extend({
	components: {
		GreatProject,
		ExpandingScrollList
	},
	props:{
		id: Number,
	},
	data(): any{
		return {
			listLength: 20,
			greatProjects: [],
		}
	},
	created(): void {
		getFile(this.id + "/greatProjects.json").then(x => this.greatProjects = x.greatProjects);
	},
	methods: {
	},
	computed: {
	}
});
