


























    import Vue from 'vue';
    import AppIcon from './appIcon.vue';
	import Bonus from './bonus.vue';

    export default Vue.extend({
        components: {
            AppIcon,
            Bonus
		},
		props:{
			religion: Object
        },
        computed: {
            
		},
		methods:{
			getReligionStyle(religion) {
				if (!religion.color || !religion.color.rgb) {
					return {};
				}
				return {
					"border": "3px solid " + religion.color.rgb,
				};
			},
		},
        data() {
            return {

			}
		}
    });
