




























































































    import Vue from 'vue';
	import AppIcon from './appIcon.vue';
	import Bonus from './bonus.vue';

    export default Vue.extend({
		components:{
			AppIcon,
			Bonus
		},
		props:{
			greatProject: Object
		},
		methods:{
			any(obj) {
				var keys = Object.keys(obj);
				if (!keys.length) {
					return false
				}
				var values = keys.map(x => obj[x]);
				return values.some(x => x && x.length);
			}
		},
		data() {
			return {

			};
		},
		computed:{
		}
    });
